<template>
  <div
    class="hoverCard"
    style="width:60%;height: 240px;padding: 20px;margin:30px auto;position: relative;box-sizing: border-box;box-shadow: 0 0 20px rgba(0,0,0,.1)">
    <div
      style="height: 200px;width:200px;box-sizing: border-box;position: absolute;left: 20px;top:20px;">
      <slot></slot>
    </div>
    <div>
      <p style="position: absolute;left: 240px;top: -20px;font-size: 30px">产品名称： {{name}}</p>
      <p style="position: absolute;left: 240px;top: 48px;font-size: 18px">产品信息： </p>
      <p style="position: absolute;left: 360px;top: 48px;font-size: 16px;width:50%;text-align: left;line-height: 30px">{{info}} </p>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'card',
    props: [
      'img', 'name', 'info'
    ]
  }
</script>
<style>
  .hoverCard:hover {
    box-shadow: 0 0 50px rgba(0,0,0,.15)!important;
  }
  .hoverCard {
    transition: .5s;
  }
</style>



